<template>
  <div>
    <fTitle>
      <template #title>Tag 标签</template>
      <template #introduce>用于标记和选择。</template>
    </fTitle>
    <sTitle>
      <template #title>动态编辑标签</template>
      <template #introduce>动态编辑标签可以通过点击标签关闭按钮后触发的 close 事件来实现</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
     <div class="grid-content">
 <el-tag
  :key="tag"
  v-for="tag in dynamicTags"
  closable
  :disable-transitions="false"
  :type="tag.type"
  :effect="tag.style"
  @close="handleClose(tag.name)">
  {{tag.name}}
</el-tag>
<el-input
  class="input-new-tag"
  v-if="inputVisible"
  v-model="inputValue"
  ref="saveTagInput"
  size="small"
  @keyup.enter.native="handleInputConfirm"
  @blur="handleInputConfirm"
>
</el-input>
<el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>

<el-dialog style="text-align: start;" width="600px" title="请选择标签类型" :visible.sync="dialogFormVisible">
  <el-form :model="dialog">
    <el-form-item label="选择标签种类">
    <el-radio-group v-model="dialog.type">
      <el-radio label="primary">默认</el-radio>
    <el-radio label="success">成功</el-radio>
    <el-radio label="info">信息</el-radio>
    <el-radio label="warning">警告</el-radio>
    <el-radio label="danger">危险</el-radio>
  </el-radio-group>
    </el-form-item>
    <el-form-item label="选择标签样式">
    <el-radio-group v-model="dialog.style">
      <el-radio label="light">荧光</el-radio>
      <el-radio label="Dark">实体</el-radio>
    <el-radio label="plain">边框</el-radio>
  </el-radio-group>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="sureChange">确 定</el-button>
  </div>
</el-dialog>
</div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      dynamicTags: [
        { name: '标签1', type: 'success', style: 'dark' },
        { name: '标签2', type: 'success', style: '' },
        { name: '标签3', type: '', style: 'dark' },
        { name: '标签4', type: 'success', style: 'plain' }
      ],
      inputVisible: false,
      inputValue: '',
      dialog: {
        type: 'primary',
        style: 'light'
      },
      dialogFormVisible: false
    }
  },
  methods: {
    sureChange () {
      const inputValue = this.inputValue

      const Value = {
        name: inputValue,
        type: this.dialog.type,
        style: this.dialog.style
      }
      this.dynamicTags.push(Value)

      this.inputVisible = false
      this.inputValue = ''
      this.dialog = {
        type: 'primary',
        style: 'light'
      }
      this.dialogFormVisible = false
    },
    handleClose (name) {
      this.dynamicTags.splice(this.dynamicTags.findIndex(item => item.name === name), 1)
      // splice(a,b)a是从哪里开始删除的索引位置,b是指从a开始删除多少个
      // indexOf()只能查询数组中的索引，无法查询对象中符合项位置
    },

    showInput () {
      this.inputVisible = true
      // this.$nextTick()指的是等待元素数据更新完之后再获取dom
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },

    handleInputConfirm () {
      if (!this.inputValue) {
        alert('请先输入标签值')
        return 0
      }
      this.dialogFormVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: start;
        .el-tag + .el-tag {
    margin-left: 10px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
      }
    }}
</style>
